import React from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../../context/AuthContext';

const Navbar = () => {
  const { user, logout } = useAuth();

  return (
    <nav className="bg-white shadow-md">
      <div className="container mx-auto px-4 py-3 flex justify-between items-center">
        <Link to="/" className="text-xl font-bold text-gray-800">Hire1Percent</Link>
        <div className="flex space-x-4">
          {user ? (
            <>
              <Link to="/dashboard" className="text-gray-600 hover:text-gray-900">Dashboard</Link>
              <Link to="/profile" className="text-gray-600 hover:text-gray-900">Profile</Link>
              <Link to="/jobs" className="text-gray-600 hover:text-gray-900">Jobs</Link>
              <Link to="/candidates" className="text-gray-600 hover:text-gray-900">Candidates</Link>
              <button onClick={logout} className="text-gray-600 hover:text-gray-900">Logout</button>
            </>
          ) : (
            <>
              <Link to="/login" className="text-gray-600 hover:text-gray-900">Login</Link>
              <Link to="/register" className="text-gray-600 hover:text-gray-900">Register</Link>
            </>
          )}
        </div>
      </div>
    </nav>
  );
};

export default Navbar; 